import { Component } from "react";

class Ico extends Component {
  state = {
    IssuedDate: "12/05/2048",
    nbCoin: 120,
    boutEnable: false
  };

  change = () => {
    this.setState({
      boutEnable: !this.state.boutEnable
    });
  };

  render() {
    const enable = this.state.boutEnable ? (
      <button className="btn btn-dark"> {this.state.nbCoin} </button>
    ) : (
      <button className="btn btn" disabled>
        {this.state.nbCoin}
      </button>
    );

    return (
      <div>
        {enable}

        <button className="btn btn mx-5vh" onClick={this.change}>
          {this.state.IssuedDate}
        </button>
      </div>
    );
  }
}

export default Ico;
